  font-family: 'CenturyGothic', sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.nav {
  width: 100%;
  position: fixed;
  background-color: rgba(20, 21, 24, 0.2);
  z-index: 20;
}

.nav__container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 10px 0px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.nav__ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}

.nav__li {
  margin: 0 59px 0 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.nav__link {
  color: #FFFFFF;
  text-decoration: none;
}

.nav__language {
  position: relative;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #14151885;
  color: #FFFFFF;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
}

.language__item {
  line-height: 60px;
  padding: 0 0 0 20px;
  line-height: 60px;
  text-align: start;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.language__item_no-act {
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #65666885;
  left: 60px;
  top: 0;
  opacity: 0;
}

.nav__language:hover {
  cursor: pointer;
}

.nav__language:hover .language__item_active {
  background-color: #14151885;
  width: 120px;
  border-radius: 41px 41px 41px 41px;
  -webkit-transition: all .1s;
  transition: all .1s;
}

.nav__language:hover .language__item_no-act {
  opacity: 1;
  -webkit-transition: all .1s;
  transition: all .1s;
}

.language__item_no-act:hover {
  background-color: #14151885;
}

.nav__link:hover {
  color: #FFFFFF;
  color: #141518;
